import ServiceProvider from '../modules/core/ServiceProvider';
import {ApolloClient} from 'apollo-client';
import {createHttpLink} from 'apollo-link-http';
import {setContext} from 'apollo-link-context';
import {InMemoryCache} from 'apollo-cache-inmemory';
import gql from 'graphql-tag';
import Cookies from 'js-cookie';

// Cookies.set( 'upi_RegistrationToken',
//     'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..kI6of0JoKc2GDfUIXk93nnR-ChxmaUeXO33A8NFqtnY' );

const httpLink = createHttpLink( {uri: 'https://w10wjonathan104.ultradent.com/api/ecommerce'} );
// const httpLink = createHttpLink( {uri: 'https://hjason1040-1.sultradent.com/api/ecommerce'} );

const authLink = setContext( ( _, {headers} ) => {
    // todo -> token needs to be able to be set at a later time
    const TOKEN = Cookies.get( 'upi_RegistrationToken' );
    return {
        headers: {
            ...headers
            // authorization: TOKEN ? `Bearer ${TOKEN}` : ''
        }
    };
} );

const client = new ApolloClient( {
        link: authLink.concat( httpLink ),
        cache: new InMemoryCache()
    } )
;

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.REGISTRATION_API,
    timeout: 90000
} );

function registrationToken ( fn ) {
    return function HOF ( {accountNumber, invoiceId} ) {
        console.log( 'Get registration token first from params:', {accountNumber, invoiceId} );

        return client
            .query( {
                variables: {accountNumber, invoiceId},
                query: ACCOUNT_TOKEN_QUERY
            } )
            // note -> let Apollo cache this token since it can change based on account params
            .then( ( {data} ) => {
                console.log( '[TOKEN]', data );
                return fn( data.getAccountTokenByAccountAndInvoice.authToken )
            } )
    }
}

// todo: add null/validation to all endpoints

const RegistrationService = {
    // @registrationToken
    authenticateAccount: registrationToken( ( accountToken ) => {

        return client
            .query( {query: CUSTOMER_ADDRESSES_QUERY} )
            .then( ( {data} ) => {
                console.log( 'data.accountAddresses', data.accountAddresses );
                return data.accountAddresses;
            } )
    } ),

    getAddresses ( params ) {
        return client
            .query( {query: CUSTOMER_ADDRESSES_QUERY} )
            .then( ( {data} ) => {
                console.log( 'data.accountAddresses', data.accountAddresses );
                return data.accountAddresses;
            } )
        // return svc.request( '/GetAddresses', {params} )
        //     .then( ServiceProvider.responseData );
    },

    generateTokenByPhone ( name, phone ) {
        return svc.request( '/CallCustomer', {
            params: {name, phone}
        } ).then( ServiceProvider.responseData );
    },

    getZipCode ( zipcode ) {
        return svc.request( '/GetZipCode', {params: {zipcode}} )
            .then( ServiceProvider.responseData )
    }
};

const CUSTOMER_ADDRESSES_QUERY = gql`
    query AddressesByAccount {
        accountAddresses {
            id
            primary
            address1
            address2
            address3
            addressType
            city
            state
            postalCode
          }
    }
`;

const ACCOUNT_TOKEN_QUERY = gql`
    query GetAccountToken($accountNumber: String!, $invoiceId: String!) {
         getAccountTokenByAccountAndInvoice(accountNumber: $accountNumber, invoiceId: $invoiceId) {
            authToken
            account {
                accountNumber
                accountStatus
                customerId
                salesChannel
            }
        }
    }
`;

export default RegistrationService;














